<template>
  <main class="main">
    <div class="container main__wrap">
      <div class="main__wrap-search">
        <input
          type="text"
          class="main__wrap-search-input"
          placeholder="Введите имя пользователя"
          v-model="name"
          @keydown.enter="getUser"
        />
        <button class="main__wrap-search-btn" @click="getUser">Search</button>
      </div>
      <button class="main__wrap-search-btn clearBtn" @click="clearSearch">
        Очистить
      </button>
      <div class="userList" v-if="users.length !== 0">
        <UserItem
          v-for="(userItem, i) in users"
          :key="i"
          :userItem="userItem"
        />
      </div>
      <div class="userList-empty" v-else>
        {{ emptyList }}
      </div>
    </div>
  </main>
</template>

<script>
import { mapState } from "vuex";
import UserItem from "@/components/UserItem.vue";

export default {
  data() {
    return {
      name: "",
      emptyList: 'Список пуст'
    };
  },
  components: {
    UserItem,
  },
  methods: {
    getUser() {
      this.$store.dispatch("getUser", this.name);
      this.name = ''
      // this.users.length == 0 ? this.emptyList = 'Ничего не найдено' : 'Список пуст'
    },
    clearSearch() {
      this.$store.commit("clear")
    },
  },
  computed: {
    ...mapState(["users", "userSelectedInfo"]),
  },
};
</script>

