import { createStore } from "vuex";
import axios from "axios";

const store = createStore({
  state() {
    return {
      apiKey: "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",
      users: [],
      followers: [],
      data: null,
      repos: [],
      follows: [],
    };
  },
  mutations: {
    getUser(state, payload) {
      state.users = payload.items;
    },
    getFollowers(state, payload) {
      state.followers = payload;
    },
    getData(state, payload) {
      state.data = payload;
      console.log(state.data);
    },
    getRepos(state, payload) {
      state.repos = payload;
    },
    getFollows(state, payload) {
      state.follows = payload;
    },
    clear(state) {
      state.users = ''
    }
  },
  actions: {
    async getUser({ commit }, userName) {
      try {
        let response = await axios.get(`https://api.github.com/search/users?q=${userName}`);
        let userInfo = {
          ...response.data,
        };
        commit("getUser", userInfo);
      } catch (error) {
        console.log("Ошибка" + error);
      }
    },
    async getFollowers({ commit }, userName) {
      try {
        let followers = await axios.get(`https://api.github.com/users/${userName.login}/followers`);
        let fov = [...followers.data];
        commit("getFollowers", fov);
      } catch (error) {
        console.log(error);
      }
    },
    async getData({ commit }, userName) {
      try {
        let createdAt = await axios.get(`https://api.github.com/users/${userName.login}`);
        let allData = [createdAt.data.created_at];
        commit("getData", allData);
      } catch (error) {}
    },
    async getRepos({ commit }, userName) {
      try {
        let reposers = await axios.get(`https://api.github.com/users/${userName.login}/repos`);
        let repos = [...reposers.data];
        commit("getRepos", repos);
      } catch (error) {
        console.log(error);
      }
    },
    async getFollows({ commit }, userName) {
      try {
        let following = await axios.get(`https://api.github.com/users/${userName.login}/following`);
        let follows = [...following.data];
        commit("getFollows", follows);
      } catch (error) {
        console.log(error);
      }
    },
  },
});

export default store;
