<template>
<Header />

<div class="container">
  <div class="userInfo__wrap">
    <div class="userInfo">
      <div class="userInfo__mainBlock">
        <div class="userInfo__mainBlock__left">
          <div class="userInfo__mainBlock__left-avtr">
            <img :src="findUser.avatar_url" alt="" />
          </div>
          <a
            class="userInfo__mainBlock__left-btn"
            :href="findUser.html_url"
            target="_blank"
            >Посетить</a
          >
        </div>
        <div class="userInfo__mainBlock__right">
          <div class="userInfo__mainBlock__right-name">
            {{ findUser.login }}
          </div>
          <div class="userInfo__mainBlock__repositories">
            Репозиториев: <span> {{ repos.length }} </span>
          </div>
          <div class="userInfo__mainBlock__right-created">
            Создан: <span>{{data[0].substr(0, 10)}}</span>
          </div>
          <div class="userInfo__mainBlock__right-followers">
            Подписщиков: <span>{{ followers.length }}</span>
          </div>
          <div class="userInfo__mainBlock__right-follows">
            Подписок: <span>{{follows.length}}</span>
          </div>
        </div>
      </div>
    </div>
    <!-- <div class="userInfo__sort">
      <div class="userInfo__sort-title">Сортировка</div>
      <div class="userInfo__sort-btns">
        <button class="userInfo__sort-btns-item">Имя</button>
        <button class="userInfo__sort-btns-item">Звезды</button>
        <button class="userInfo__sort-btns-item">Дата</button>
      </div>
    </div> -->
    <div class="userInfo__repos">Репозитории</div>
    <div class="userInfo__repositoriesList">
      <div class="userInfo__repositoriesList__item" v-for="(reposItem, i) in repos.slice(0, 5)" :key="i">
        <div class="userInfo__repositoriesList__item__info">
          <div class="userInfo__repositoriesList__item__info-name">{{reposItem.name}}</div>
          <div class="userInfo__repositoriesList__item__info-stars">
            Кол-во звёзд: <span>{{ reposItem.watchers }}</span>
          </div>
          <div class="userInfo__repositoriesList__item__info-added">
            Дата добавления: <span>{{ reposItem.pushed_at.substr(0, 10) }}</span>
          </div>
        </div>
        <a class="userInfo__repositoriesList__item-btn" :href="reposItem.clone_url" target="_blank">Посетить</a>
      </div>
    </div>
  </div>
</div>
  <br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br />
</template>

<script>
import UserItem from "@/components/UserItem.vue";
import Header from "@/components/Header.vue";
import { mapState, mapActions } from "vuex";

export default {
  data() {
    return {};
  },

  components: {
    UserItem,
    Header,
  },

  methods: {
    ...mapActions(["getFollowers", "getData", "getRepos", "getFollows"]),
    show() {
      console.log(this.data);
    },
  },
  computed: {
    ...mapState(["users", "followers", "data", "repos", "follows"]),
    findUser() {
      return this.users.find((user) => this.id == user.id);
    },
  },
  created() {
    let id = this.$route.params.id;
    this.id = id;
    this.getFollowers(this.findUser);
    this.getData(this.findUser);
    this.getRepos(this.findUser)
    this.getFollows(this.findUser)
  },
};
</script>

